const formElements = (editForm) => {
	let formElements = {
		client: {
			elementType: 'input',
			elementConfig: {
				label: '',
				required: true
			},
			extra: {
				autoComplete: 'clients',
				addButton: true
			},
			value: ''
		},
		date: {
			elementType: 'date',
			elementConfig: {
				label: '',
				required: true
			},
			value: new Date()
		},
		department: {
			elementType: 'input',
			elementConfig: {
				label: ''
			},
			extra: {
				autoComplete: 'departments',
				addButton: true
			},
			value: ''
		},
		info: {
			elementType: 'input',
			elementConfig: {
				label: '',
				required: true,
				multiline: true,
				id: "standard-multiline-static",
				rows: "2"
			},
			value: ''
		}
	}

	if (editForm) {
		formElements = {
			...formElements,

			status: {
				elementType: 'select',
				elementConfig: {
					label: '',
					options: []
				},
				value: ''
			}
		}
	}
	return formElements
}

export default formElements