const formElements = (editForm) => {
	let formElements = {
		filename: {
			elementType: 'input',
			elementConfig: {
				label: ' ',
				required: true
			},
			value: ''
		},
		task_dty: {
			elementType: 'input',
			elementConfig: {
				label: ' ',
				required: true
			},
			extra: {
				autoComplete: 'tasksDTY',
				addButton: true
			},
			value: ''
		},
		date: {
			elementType: 'date',
			elementConfig: {
				label: '',
				required: true
			},
			value: new Date()
		},
		ip: {
			elementType: 'input',
			elementConfig: {
				label: 'IP'
			},
			value: ''
		},
		type_of_device: {
			elementType: 'select',
			elementConfig: {
				label: '',
				options: []
			},
			value: ''
		},
		spare_parts: {
			elementType: 'input',
			elementConfig: {
				label: '',
				multiline: true,
				id: "standard-multiline-static",
				rows: "2"
			},
			value: ''
		},
		comment: {
			elementType: 'input',
			elementConfig: {
				label: '',
				multiline: true,
				id: "standard-multiline-static",
				rows: "2"
			},
			value: ''
		},
		text: {
			elementType: 'input',
			elementConfig: {
				label: '',
				required: true,
				multiline: true,
				id: "standard-multiline-static",
				rows: "5",
				fullWidth: true
			},
			value: '                    :'
		},
		generate_pdf: {
			elementType: 'checkbox',
			elementConfig: {
				label: ' PDF'
			},
			value: false
		},
	}
	return formElements
}

export default formElements