const formElements = (editForm) => {
	let formElements = {
		date_from: {
			elementType: 'date',
			elementConfig: {
				label: ''
			},
			value: null
		},
		date_to: {
			elementType: 'date',
			elementConfig: {
				label: ''
			},
			value: null
		},
		client: {
			elementType: 'input',
			elementConfig: {
				label: '',
			},
			extra: {
				autoComplete: 'clients'
			},
			value: ''
		},
		department: {
			elementType: 'input',
			elementConfig: {
				label: '',
			},
			extra: {
				autoComplete: 'departments'
			},
			value: ''
		},
		ip: {
			elementType: 'input',
			elementConfig: {
				label: 'IP',
			},
			value: ''
		},
		type_of_device: {
			elementType: 'select',
			elementConfig: {
				label: '',
				options: []
			},
			value: ''
		},
		sent: {
			elementType: 'select',
			elementConfig: {
				label: '',
				options: [
					{
						id: 0,
						name: ''
					},
					{
						id: 1,
						name: ''
					},
					{
						id: 2,
						name: ' '
					}
				]
			},
			value: 0
		},
	}

	return formElements
}

export default formElements