const formElements = (editForm) => {
	let formElements = {
		app: {
			elementType: 'select',
			elementConfig: {
				label: '',
				options: []
			},
			value: ''
		},
		name: {
			elementType: 'input',
			elementConfig: {
				label: '',
				required: true
			},
			value: ''
		}
	}
	return formElements
}

export default formElements