const formElements = (editForm) => {
	let formElements = {
		date_from: {
			elementType: 'date',
			elementConfig: {
				label: '',
			},
			value: null
		},
		date_to: {
			elementType: 'date',
			elementConfig: {
				label: '',
			},
			value: null
		},
		status: {
			elementType: 'select',
			elementConfig: {
				label: '',
				options: []
			},
			value: ''
		},
		client: {
			elementType: 'input',
			elementConfig: {
				label: '',
			},
			extra: {
				autoComplete: 'clients'
			},
			value: ''
		},
		ip: {
			elementType: 'input',
			elementConfig: {
				label: 'IP',
			},
			value: ''
		},
		tech: {
			elementType: 'input',
			elementConfig: {
				label: '',
			},
			extra: {
				autoComplete: 'techs'
			},
			value: ''
		}
	}

	return formElements
}

export default formElements